* {
  font-family: Roboto;
}

@media screen and (max-width: 767px) {
  .header-container {
    width: 100%;
    height: 230px;
    background-image: url("FondoJPG.501ad1fa.jpg");
    margin: 0 auto;
  }

  .icon {
    fill: #fff;
  }

  .nav-main {
    justify-content: space-between;
    display: flex;
  }

  .logo-name {
    align-items: baseline;
    margin-top: 48px;
    margin-left: 23px;
    display: flex;
  }

  .nav-options {
    margin-top: 48px;
    margin-right: 20px;
  }

  .list-nav {
    gap: 39px;
    list-style: none;
    display: flex;
  }

  .list-nav a {
    color: #fff;
  }

  .search-o {
    justify-content: center;
    margin-top: 38px;
    margin-left: 21px;
    display: flex;
  }

  .logo {
    display: none;
  }

  .footer-container {
    height: 100px;
    background-color: #f7f7f7;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .footer-mobile {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    display: flex;
  }

  .fotter-top-messa {
    margin-top: 28.7px;
    margin-bottom: 10px;
  }

  .fotter-top-messa p, .footer-bottom p {
    margin-top: 0;
    margin-bottom: 0;
  }

  .nav-underline a:after {
    content: "";
    width: 100%;
    height: 4px;
    opacity: 0;
    background-color: #ff6b08;
    margin-top: 1px;
    position: absolute;
    top: 100%;
    left: 0;
  }

  .nav-underline a {
    text-decoration: none;
    position: relative;
  }

  .nav-underline a:hover:after, .nav-underline a.selected:after {
    opacity: 1;
  }
}

