body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  margin: 60px auto 0;
  padding: 0 20px;
}

h1 {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 20px;
  font-size: 2.5rem;
}

.peliculas {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.pelicula {
  width: 100%;
  border-radius: 5px;
  margin-bottom: 20px;
  transition: all .3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.pelicula:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, .4);
}

@media only screen and (min-width: 768px) {
  .pelicula {
    width: calc(50% - 10px);
  }
}

@media only screen and (min-width: 1024px) {
  .pelicula {
    width: calc(33.33% - 10px);
  }
}

.pelicula__imagen {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  display: block;
}

.pelicula__titulo {
  margin: 0;
  padding: 10px;
  font-size: 1.5rem;
}

.pelicula__estreno {
  color: #ff6b08;
  margin: 0;
  padding: 10px;
  font-family: Roboto;
  font-size: 1.5rem;
  font-style: normal;
}

.pelicula__genero {
  color: #ff6b08;
  margin: 0;
  padding: 10px;
  font-family: Roboto;
  font-size: 1rem;
  font-style: normal;
}

